




































































































































































import Vue from "vue";
import axios from "@/plugins/axios";
import moment from "moment";

export default Vue.extend({
  name: "member_overview",
  components: {},
  data() {
    const search: { [k: string]: string } = {};
    return {
      moment,
      searchSider: true,
      dialogFormVisible: false,
      advancedSearch: false,
      selectAll: { checkAll: false, isIndeterminate: false },
      tabelAlert: {
        text: this.$t("search query info"),
        type: "info"
      },
      items: [],
      total: 0,
      currentPage: 1,
      settingList: {
        status: [],
        grade: [],
        reward: [],
        commission: []
      },
      search,
      enrollmentdate: [],
      therole: [],
      orderby: "",
      sort: "",
      editNoteDlg: {
        visible: false,
        account: "",
        note: ""
      }
    };
  },
  computed: {},
  created() {
    // this.fetchData();
    // console.log(this.change("123"));
    // console.log(this.$refs.form);
    this.getStatus();
    this.getGrade();
    this.getReward();
    this.getCommission();
    this.fetchData();
  },
  methods: {
    // change(a: string) {
    //   return a + " test";
    // },
    // consoleLength<T>(a: T[]): T[] {
    //   console.log(a.length);
    //   return a;
    // },
    collapse() {
      this.searchSider = !this.searchSider;
    },
    goto(target: string) {
      location.href = target;
    },
    fetchData() {
      const searchParams = Object.keys(this.search).reduce(
        (acc: { [k: string]: string }, cur: string) => {
          if (this.search[cur]) acc[cur] = this.search[cur];
          return acc;
        },
        {}
      );
      // if (searchParams.grade) {
      //   searchParams.grade = JSON.stringify(searchParams.grade);
      // }
      const params = {
        page: this.currentPage,
        ...searchParams,
        // eslint-disable-next-line @typescript-eslint/camelcase
        start_date: this.enrollmentdate ? this.enrollmentdate[0] : "",
        // eslint-disable-next-line @typescript-eslint/camelcase
        end_date: this.enrollmentdate ? this.enrollmentdate[1] : "",
        therole: this.therole ? this.therole[0] : "",
        orderby: this.orderby,
        sort: this.sort
      };
      axios
        .get("/member-overview/member", { params })
        .then(response => {
          // console.log(response);
          this.items = response.data.data;
          this.total = response.data.total;
        })
        .catch(error => {
          if (error) {
            this.tabelAlert = {
              text:
                error.response.status == 404
                  ? this.$t("no_data")
                  : error.response.data.message,
              type: "warning"
            };
            this.items = [];
            this.total = 0;
          }
        });
    },
    sortChange(val: { prop: string; order: string }) {
      this.orderby = val.prop;
      switch (val.order) {
        case "ascending":
          this.sort = "asc";
          break;
        case "descending":
          this.sort = "desc";
          break;
        default:
          this.orderby = "";
          this.sort = "";
          break;
      }
      this.currentPage = 1;
      this.fetchData();
    },
    editSetting(
      account: string,
      target: string,
      value: string,
      callback: Function
    ) {
      // console.log(account + ":" + value);
      const params: { [k: string]: string } = {};
      params[target] = value;
      axios
        .put("/member-overview/member/edit/" + account, params)
        .then(response => {
          // console.log(response);
          alert(response.data.message);
          this.fetchData();
          callback ? callback() : "";
        })
        .catch(error => {
          if (error) {
            // console.log(error);
            alert(error.data.message);
          }
        });
    },
    getStatus() {
      axios
        .get("/member-overview/setting-list/status")
        .then(response => {
          // console.log(response);
          this.settingList.status = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getGrade() {
      axios
        .get("/member-overview/setting-list/grade")
        .then(response => {
          // console.log(response);
          this.settingList.grade = response.data.filter(
            (item: { status: number }) => item.status === 1
          );
        })
        .catch(function(error) {
          console.log(error.response.data.details.message);
        });
    },
    getReward() {
      axios
        .get("/member-overview/setting-list/reward")
        .then(response => {
          const res = response.data.reduce(
            (
              acc: { [k: string]: { status: number; name: string } },
              cur: { status: number; name: string }
            ) => {
              if (!Object.keys(acc).includes(cur.name))
                acc[cur.name] = { status: cur.status, name: cur.name };
              else if (acc[cur.name].status !== 1 && cur.status == 1)
                acc[cur.name].status = 1;
              return acc;
            },
            {}
          );
          this.settingList.reward = Object.values(res);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getCommission() {
      axios
        .get("/member-overview/setting-list/commission")
        .then(response => {
          const res = response.data.reduce(
            (
              acc: { [k: string]: { status: number; name: string } },
              cur: { status: number; name: string }
            ) => {
              if (!Object.keys(acc).includes(cur.name))
                acc[cur.name] = { status: cur.status, name: cur.name };
              else if (acc[cur.name].status !== 1 && cur.status == 1)
                acc[cur.name].status = 1;
              return acc;
            },
            {}
          );
          this.settingList.commission = Object.values(res);
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
});
